html {
  position: relative;
  min-height: 100%;
}

body {
  /* Margin bottom by footer height */
  margin-bottom: 80px;
}

.navbar {
  margin-bottom: 0;
}

.breadcrumb {
  font-family: "Oxygen Mono", monospace;
  background-color: transparent;
}

.jumbotron {
  padding-top: 4rem;
  padding-bottom: 1rem;
  margin-bottom: 0;
  background-color: #fff;
}

.jumbotron p:last-child {
  margin-bottom: 0;
}

.jumbotron-heading {
  font-weight: 300;
}

.jumbotron-heading .elm-pipe {
  color: #60b5cc;
  font-weight: bold;
  font-family: "Share Tech Mono", monospace;
}

.jumbotron-heading .elm-gt {
  color: #f0ad00;
  font-weight: bold;
  font-family: "Share Tech Mono", monospace;
}

.jumbotron .container {
  max-width: 80rem;
}

.card {
  padding: 0.75rem;
  margin-bottom: 2rem;
  border: 0;
  background-color: #f5f5f5;
}

.card-block {
  padding-right: 0.4rem;
  padding-left: 0.4rem;
}

.card-title {
  font-family: "Orbitron", sans-serif;
}

.card-title .fa {
  font-size: 10px;
  margin-right: 20px;
  position: relative;
  top: -4px;
}

.card-text {
}

.card .info {
  min-height: 9em;
  font-size: 1.15rem;
  font-family: "Bad Script", cursive;
}

.demo-content {
  padding-bottom: 30px;
}

.footer {
  padding-top: 2em;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 82px;
  background-color: #222;
  border-color: #080808;
}

.footer a {
  color: #bbb;
}

.footer a:hover {
  color: #fff;
}

.footer li {
  display: inline;
  padding-right: 1em;
}

.iconic {
  width: 140px;
  height: 140px;
  background-color: #eceeef;
  border-radius: 100%;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}
